@import "../../../App.scss";

#booking-header{
    background:$table-header-color;
    border-radius: 13px;
    padding-top: 5px;
    padding-left: 20px;
    thead {
        font-family: Poppins;
        font-style: normal;
        font-weight: normal;
        font-size: 16px;
        line-height: 24px;
        color: #686868;
    }
    th {
        padding-bottom: 20px;
    }
}

 
#booking-header th:nth-child(1) {
    width: 150px;
}
#booking-header th:nth-child(2) {
    width: 180px !important;
}

#booking-header th:nth-child(3) {
    width: 150px !important;
}

#booking-header th:nth-child(4) {
    width: 230px !important;
} 